@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200');

.addBtn {
	display: flex;
	justify-content: center;
	align-items: center;
	align-self: center;

	width: 40px;
	height: 40px;

	margin-top: -40px;

	box-sizing: border-box;
	border: solid 2px #3b3530;
	border-radius: 0 0 15px 0;
	border-radius: 40px;

	background-color: rgb(255, 213, 0);

	cursor: pointer;

	visibility: none;

	transform: scale(0.8);
	opacity: 0.5;

	&:hover {
		opacity: 1;
	}
}

.add-icon {
	width: 25px;
	height: 25px;
}

#add-skill {
	align-self: baseline;
	margin-left: 154px;
	margin-top: -33px;
}
